import React, { useContext, useEffect, useState } from 'react';

// Material UI
import { TextField } from '@material-ui/core';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

// Custom Components
import BackgroundCard from '../../../components/BackgroundCard';
import ClientDrawer from '../../../components/ClientDrawer';
import PrimaryButton from '../../../components/Button';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';
import { SnackContext } from '../../../contexts/SnackContext';

import api from '../../../services/api';

// Assets
import './index.css';

const SuppliersCreate = () => {
  const { setSnack } = useContext(SnackContext);

  const [cpfCnpjLabel, setCpfCnpjLabel] = useState("CNPJ");
  const [cpf_cnpj, setCpfCnpj] = useState("");
  const [type, setType] = useState("PJ");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");

  useEffect(() => {
    document.title = 'GestFacil | Cadastro de Fornecedor';
  }, []);

  const resetForm = () => {
    setCpfCnpj("");
    setName("");
    setEmail("");
    setPhone("");
  }

  const handleTypeChange = (e) => {
    setType(e.target.value);
    if (type === 'PF') {
      setCpfCnpjLabel("CNPJ");
    } else {
      setCpfCnpjLabel("CPF");
    }
  }

  const handleCpfCnpjChange = (e) => {
    setCpfCnpj(e.target.value);
  }

  const handleNameChange = (e) => {
    setName(e.target.value);
  }

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  }

  const handlePhoneChange = (e) => {
    setPhone(e.target.value);
  }

  async function handleCreate(e) {
    
    e.preventDefault();

    try {
      await api.post('/supplier', {
        type,
        cpf_cnpj,
        name,
        email,
        phone,
      });
      setSnack({ message: 'Fornecedor cadastrado com sucesso!', type: 'success', open: true })
      resetForm();

    } catch (err) {
      if(!err){       
        setSnack({ message: 'Encontramos um erro ao cadastrar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (
    <ClientDrawer title="Fornecedores / Cadastro">
      <BackgroundCard>
        <HeaderTitle
          title="Cadastro de Fornecedor"
          subtitle="Ateno: preecha todos os campos obrigatrios (*) para o cadastro."
        />

        <div className="accounting-form-container">
          <form className="accounting-form" method="POST" onSubmit={handleCreate}>
            <HeaderSubtitle
              title="Dados do Fornecedor"
            />           

            <div className="accounting-form-admin">
              <div className="input-block">
                <FormControl component="fieldset">
                  <FormLabel component="legend">Tipo</FormLabel>
                  <RadioGroup row aria-label="tipo" name="type" value={type} onChange={handleTypeChange}>
                    <FormControlLabel value="PF" control={<Radio />} label="Pessoa Fsica" />
                    <FormControlLabel value="PJ" control={<Radio />} label="Pessoa Jurdica" />                                   
                  </RadioGroup>
                </FormControl>
              </div>
              <div className="input-block">
                <TextField
                  name="cpf_cnpj"
                  label={cpfCnpjLabel}
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={cpf_cnpj}
                  onChange={handleCpfCnpjChange}                  
                  fullWidth
                  required="required"
                />
              </div>
              <div className="input-block">                
                <TextField
                  name="name"
                  label="Nome"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={name}
                  onChange={handleNameChange}                  
                  fullWidth
                  required="required"
                />
              </div>
              <div className="input-block">
                <TextField
                  name="email"
                  label="Email"
                  variant="outlined"
                  type="email"
                  autoComplete="off"
                  value={email}
                  onChange={handleEmailChange}                                
                  fullWidth
                />
              </div>
              <div className="input-block">
                <TextField
                  name="phone"
                  label="Telefone"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={phone}
                  onChange={handlePhoneChange}                              
                  fullWidth
                />
              </div>
            </div>

            <div className="accounting-form-button">
              <PrimaryButton size="large" variant="contained" type="submit">Cadastrar</PrimaryButton>
            </div>
          </form>
        </div>
      </BackgroundCard>
    </ClientDrawer>
  );
};

export default SuppliersCreate;