import React, { useContext, useEffect, useState } from 'react';

import { TextField } from '@material-ui/core';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

import BackgroundCard from '../../../components/BackgroundCard';
import ClientDrawer from '../../../components/ClientDrawer';
import PrimaryButton from '../../../components/Button';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';
import { SnackContext } from '../../../contexts/SnackContext';

import api from '../../../services/api';

import './index.css';

const SubcategoriesCreate = () => {
  const { setSnack } = useContext(SnackContext);

  const [title, setTitle] = useState("");
  const [type, setType] = useState("RECEITA");


  useEffect(() => {
    document.title = 'GestFacil | Cadastro de Subcategoria';
  }, []);

  const resetForm = () => {
    setTitle("");
    setType("RECEITA");
  }

  const handleTypeChange = (e) => {
    setType(e.target.value);
  }

  const handleTitleChange = (e) => {
    setTitle(e.target.value);
  }

  {/*async function handleCreate(e) {
    
    e.preventDefault();

    try {
      await api.post('/supplier', {
        type,
        cpf_cnpj,
        name,
        email,
        phone,
      });
      setSnack({ message: 'Fornecedor cadastrado com sucesso!', type: 'success', open: true })
      resetForm();

    } catch (err) {
      if(!err){       
        setSnack({ message: 'Encontramos um erro ao cadastrar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }*/}

  return (
    <ClientDrawer title="Subcategorias / Cadastro">
      <BackgroundCard>
        <HeaderTitle
          title="Cadastro de Subcategoria"
          subtitle="Ateno: preecha todos os campos obrigatrios (*) para o cadastro."
          previousPage="/subcategories"
          showBackButton
        />

        <div className="accounting-form-container">
          <form className="accounting-form" method="POST" onSubmit={null}>
            <HeaderSubtitle
              title="Dados da Subcategoria"
            />

            <div className="accounting-form-admin">
              <div className="input-block">
                <FormControl component="fieldset">
                  <FormLabel component="legend">Tipo</FormLabel>
                  <RadioGroup row aria-label="tipo" name="type" value={type} onChange={handleTypeChange}>
                    <FormControlLabel value="RECEITA" control={<Radio />} label="Receita" />
                    <FormControlLabel value="DESPESA" control={<Radio />} label="Despesa" />                                   
                    <FormControlLabel value="ATIVO" control={<Radio />} label="Ativo" />                                   
                  </RadioGroup>
                </FormControl>
              </div>
              <div className="input-block">
                <TextField
                  name="title"
                  label="Ttulo"
                  placeholder="Ex.: Mercadorias para Revenda"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={title}
                  onChange={handleTitleChange}                  
                  fullWidth
                  required="required"
                />
              </div>             
            </div>

            <div className="accounting-form-button">             
              <PrimaryButton size="large" variant="contained" type="submit">
                Cadastrar
              </PrimaryButton>
            </div>
          </form>
        </div>
      </BackgroundCard>
    </ClientDrawer>
  );
};

export default SubcategoriesCreate;