import React, { useContext, useEffect } from 'react';
import { Redirect } from 'react-router-dom';

// Material UI
import Box from '@material-ui/core/Box';

// Icons
import AddIcon from '@material-ui/icons/Add';
import SearchOutlinedIcon from '@material-ui/icons/SearchOutlined';

// Custom Components
import CardLink from '../../../components/CardLink';
import BackgroundCard from '../../../components/BackgroundCard';
import AccountingDrawer from '../../../components/AccountingDrawer';
import { HeaderTitle } from '../../../components/HeaderTitle';
import { AuthContext } from '../../../contexts/AuthContext';

// Assets
import './index.css';

const Calls = () => {
  const { user, role } = useContext(AuthContext);
  
  useEffect(() => {
    if(role !== 'accounting') {
      <Redirect to="/"/>
    }
    document.title = 'GestFacil | Dashboard';
  }, [role])

  return (
    <AccountingDrawer title="Dashboard">
      <BackgroundCard>
        <div className="master-dashboard">
          <HeaderTitle
            title="Chamados"
          />

          <Box
            display="flex"
            flexWrap="wrap"
            flexDirection="row"
            justifyContent="left"
            alignItems="left"
            css={{ marginTop: 20 }}
          >
            <CardLink link="/acc/create_call" icon={<AddIcon/>}>
              Abrir Chamado
            </CardLink>        
            
            {/* <CardLink link="/acc/search_call" icon={<SearchOutlinedIcon/>}>
              Consultar
            </CardLink> */}
          
          </Box>

        </div>
      </BackgroundCard>
    </AccountingDrawer>

  );
};


export default Calls;