import React, { useEffect } from 'react';

// Material UI
import Box from '@material-ui/core/Box';

// Icons
import BusinessOutlinedIcon from '@material-ui/icons/BusinessOutlined';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';

// Custom Components
import CardLink from '../../../components/CardLink';

// Assets
import mainLogo from '../../../assets/img/logo-small.png';
import './index.css';

const ChooseModule = () => {

  useEffect(() => {
    document.title = 'Seleo de Mdulo | GestFacil'
  }, [])

  return (
    <div className="chooseModule">
      <Box
        display="flex"
        flexDirection="row"
        justifyContent="center"
        alignItems="center"
        css={{ height: 35 + 'vh' }}
      >
        <img className="responsive-logo" src={mainLogo} alt="Gest Facil" />
      </Box>

      <Box
        display="flex"
        flexWrap="wrap"
        flexDirection="row"
        justifyContent="center"
        alignItems="center"
      >

        {/* import LockOutlinedIcon from '@material-ui/icons/LockOutlined'; */}
        {/* <CardLink link="/master/login" icon={<LockOutlinedIcon/>}>
          GF Master
        </CardLink> */}

        <CardLink link="/acc/login" icon={<BusinessOutlinedIcon/>}>
          Escritrio
        </CardLink>

        <CardLink link="/cli/login" icon={<PersonOutlineOutlinedIcon/>}>
          Cliente
        </CardLink>

        {/* <CardLink link="/gfmaster/login" icon="search">
            Consultar Processo
          </CardLink> */}
      </Box>
    </div>
  );
};

export default ChooseModule;