import React, { useContext, useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core'
import { Skeleton } from '@material-ui/lab';

import ClientDrawer from '../../../components/ClientDrawer';
import BackgroundCard from '../../../components/BackgroundCard';
import PrimaryButton from '../../../components/Button';
import BackgroundCardHeader from '../../../components/BackgroundCardHeader';
import Modal from '../../../components/Modal';
import IconButton from '../../../components/IconButton';
import { DataGrid, gridColumnLookupSelector } from '@material-ui/data-grid';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

import AddIcon from '@material-ui/icons/Add';
import CreateOutlinedIcon from '@material-ui/icons/CreateOutlined';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import WbIncandescentIcon from '@material-ui/icons/WbIncandescentOutlined';
import PowerSettingsNewOutlinedIcon from '@material-ui/icons/PowerSettingsNewOutlined';

import './index.css';
import api from '../../../services/api';
import { SnackContext } from '../../../contexts/SnackContext';

const useStyles = makeStyles({
  table: {
    minWidth: 'auto',
    border: '1px solid rgba(224, 224, 224, 1)',
    borderCollapse: 'separate',
    borderRadius: '8px'
  },
});

const formatStatus = (active) => { 
  return active ? 'ATIVO' : 'INATIVO';
}

const FilesList = () => {
  const classes = useStyles();
  const history = useHistory();

  const { setSnack } = useContext(SnackContext);

  const [loading, setLoading] = useState(true);
  const [openModal, setOpenModal] = useState(false);

  const [method, setMethod] = useState('');
  const [supplierId, setSupplierId] = useState('');
  
  const [suppliers, setSuppliers] = useState('');
  
  const columns = [  
    { 
      field: 'name', 
      headerName: 'Nome', 
      width: 250,
      sortable: false,
    },
    { 
      field: 'cpf_cnpj', 
      headerName: 'CPF/CNPJ', 
      width: 160,
      sortable: false,
    },
    { 
      field: 'email', 
      headerName: 'E-mail', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'phone', 
      headerName: 'Telefone', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'active', 
      headerName: 'Status', 
      width: 120,
      sortable: false,
      renderCell: (obj) => {
        return formatStatus(obj.row.active);
      }
    },
    { 
      field: 'actions', 
      headerName: 'Aes', 
      width: 200,
      sortable: false,
      renderCell: (supplier) => {        
        return (<>  
         <IconButton onClick={() => history.push(`/cli/edit_supplier/${supplier.row.id}`)}>
            <CreateOutlinedIcon />
          </IconButton>
          {
            supplier.row.active 
            ?
            <IconButton onClick={() => handleModalOpen(supplier.row.id,'inactivate')}>
              <PowerSettingsNewOutlinedIcon/> 
            </IconButton>
             :
            <>
              <IconButton onClick={() => handleModalOpen(supplier.row.id,'delete')}>
                <DeleteOutlinedIcon />
              </IconButton>
              <IconButton onClick={() => handleActivate(supplier.row.id)}>
                <WbIncandescentIcon />
              </IconButton>
            </>
          }
        </>)
      }
    }
  ];

  useEffect(() => {
    document.title = 'Fornecedores | GestFacil';    
    populateTable();
  }, []);

  function handleModalOpen(supplier, method) {
    setSupplierId(supplier);
    setMethod(method);    
    setOpenModal(true);
  }
  
  const handleCloseModal = () => {
    setOpenModal(false);
  };

  const handleSupplierChange = (data) => {
    setSuppliers(data);
  };

  const handleDelete = async () => {
    try {
      await api.delete(`/supplier/${supplierId}`);
      handleCloseModal();
      populateTable();
      setSnack({ message: 'Fornecedor deletado com sucesso!', type: 'success', open: true});
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao deletar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  const handleInactivate = async () => {
    try {
      await api.post(`/supplier/inactivate/${supplierId}`);
      handleCloseModal();
      populateTable();
      setSnack({ message: 'Fornecedor inativado com sucesso!', type: 'success', open: true});
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao inativar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  const handleActivate = async (id) => {
    try {
      await api.post(`/supplier/activate/${id}`);
      handleCloseModal();
      populateTable();
      setSnack({ message: 'Fornecedor ativado com sucesso!', type: 'success', open: true});
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao  ativar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }
 
  {/*const handleStatusChange = (e) => {
    console.log(e);
    setStatus(e.target.value);
    console.log(status);
    handleFilter();
    setInterval(() => {
      console.log(status);
    }, 1000)
  }*/}

  // const handleFilter = async () => { 
  //   console.log(status);
  //   let filteredSuppliers = []
  //   if (status === "All") {
  //     setRows(suppliers);
  //   } else if (status === "Actives") {     
  //     filteredSuppliers = suppliers.filter(el => {
  //       if (el.active) return el;
  //     });  
  //     setRows(filteredSuppliers);
  //   } else {
  //     filteredSuppliers = suppliers.filter(el => {
  //       if (!el.active) return el;
  //     });
  //     setRows(filteredSuppliers);
  //   }   
  // };

  {/*async function handleDelete() {
    try {     
      console.log(fileId)  ;
      const response = await api.delete(`/client_file/${fileId}`);
      setLoading(true);
      populateTable();
      handleCloseModal();
      setSnack({ message: 'Arquivo excludo com sucesso!', type: 'success', open: true});  
    } catch(err) {
      if(!err.response){
        setSnack({ message: 'Encontramos um erro ao excluir. Entre em contato com o suporte! ' + err, type: 'error', open: true});
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }*/}

  async function populateTable() {
    try {
      const response = await api.get(`/supplier`);
      if (response.data.suppliers){         
        handleSupplierChange(response.data.suppliers);    
      } else {
       setSuppliers([]);
      }        
      setLoading(false);
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao buscar os Fornecedores. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (

    <ClientDrawer title="Fornecedores">
      <div className="master-dashboard">
        <BackgroundCard>        
          <BackgroundCardHeader
            title="Fornecedores"
          >
            <Link className="buttonLink" to="/cli/create_supplier"><PrimaryButton variant="contained" size="large"><AddIcon /></PrimaryButton></Link>
          </BackgroundCardHeader>

          {/*<div style={{marginTop: '10px'}}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Status</FormLabel>
              <RadioGroup row aria-label="categoria" name="category" value={status} onChange={handleStatusChange}>
                <FormControlLabel value="Actives" control={<Radio />} label="Ativo" />
                <FormControlLabel value="Inactives" control={<Radio />} label="Inativo" />
                <FormControlLabel value="All" control={<Radio />} label="Todos" />                
              </RadioGroup>
            </FormControl>
          </div>*/}

          <div style={{ height: '75vh', width: '100%', marginTop: '40px'}}>

          
           
              {(loading) ?
                <>
                  <Skeleton animation="wave" width="auto" height="57px" variant="rect" />

                  {[1, 2, 3, 4, 5, 6].map((n) =>
                    <Skeleton key={n} animation="wave" width="auto" height="65px" variant="rect" style={{ marginTop: 5 }} />
                  )}

                </>
                :
                
                <DataGrid rows={suppliers} columns={columns} pageSize={10} disableColumnMenu disableSelectionOnClick />
                
              }
            
          </div>

        </BackgroundCard>
      </div>
      <Modal
          open={openModal}
          onClose={handleCloseModal}
          title={`Tem certeza que deseja ${method === 'delete' ? 'excluir' : 'inativar'} o fornecedor?`}
          actions={
            <>
              <PrimaryButton onClick={method === 'delete' ? handleDelete : handleInactivate}>Sim</PrimaryButton>
              <PrimaryButton onClick={handleCloseModal} color="primary">No</PrimaryButton>
            </>
          }
          >
          {method === 'delete' ? <p><strong>Cuidado:</strong> Esta ao  irreversvel!</p> : ''}
      </Modal>
    </ClientDrawer>

  );
};

export default FilesList;