import React, { useContext, useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';

// Material UI
import { TextField, MenuItem } from '@material-ui/core';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

// Custom Components
import BackgroundCard from '../../../components/BackgroundCard';
import ClientDrawer from '../../../components/ClientDrawer';
import PrimaryButton from '../../../components/Button';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';
import { SnackContext } from '../../../contexts/SnackContext';

import api from '../../../services/api';

// Assets
import './index.css';

const SuppliersCreate = () => {
  const { setSnack } = useContext(SnackContext);

  const { id } = useParams();
  const history = useHistory();

  const [cpfCnpjLabel, setCpfCnpjLabel] = useState("CNPJ");
  const [cpf_cnpj, setCpfCnpj] = useState("");
  const [type, setType] = useState("PJ");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [active, setActive] = useState(true);

  useEffect(() => {
    document.title = 'GestFacil | Edio de Fornecedor';
    populateFields();
  }, []);

  useEffect(() => {
    handleLabelChange();
  }, [type]);

  const resetForm = () => {
    setCpfCnpj("");
    setName("");
    setEmail("");
    setPhone("");
  }

  const handleLabelChange = () => {
    if (type === 'PJ') {
      setCpfCnpjLabel("CNPJ");
    } else {
      setCpfCnpjLabel("CPF");
    }
  }

  const handleTypeChange = (e) => {
    setType(e.target.value); 
    handleLabelChange();   
  }

  const handleCpfCnpjChange = (e) => {
    setCpfCnpj(e.target.value);
  }

  const handleNameChange = (e) => {
    setName(e.target.value);
  }

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  }

  const handlePhoneChange = (e) => {
    setPhone(e.target.value);
  }

  const handleActiveChange = (e) => {
    setActive(e.target.value);
  }

  const populateFields = async () => {
    try {
      const response = await api.get(`/supplier/${id}`);
      const supplier = response.data.supplier;

      setType(supplier.type);
      setCpfCnpj(supplier.cpf_cnpj);
      setName(supplier.name);
      setEmail(supplier.email);
      setPhone(supplier.phone);
      setActive(supplier.active);

    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao buscar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
        history.push("/cli/suppliers");
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
        history.push("/cli/suppliers");
      }
    }  
  }

  const handleEdit = async (e) => {
    
    e.preventDefault();

    try {
      await api.put(`/supplier/${id}`, {
        type,
        cpf_cnpj,
        name,
        email,
        phone,
        active,
      });
      setSnack({ message: 'Fornecedor atualizado com sucesso!', type: 'success', open: true })
      history.push("/cli/suppliers");

    } catch (err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao atualizar o fornecedor. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (
    <ClientDrawer title="Fornecedores / Edio">
      <BackgroundCard>
        <HeaderTitle
          title="Edio de Fornecedor"
          subtitle="Ateno: mantenha os campos obrigatrios (*) preenchidos."
        />

        <div className="accounting-form-container">
          <form className="accounting-form" method="POST" onSubmit={handleEdit}>
            <HeaderSubtitle
              title="Dados do Fornecedor"
            />           

            <div className="accounting-form-admin">
              <div className="input-block">
                <FormControl component="fieldset">
                  <FormLabel component="legend">Tipo</FormLabel>
                  <RadioGroup row aria-label="tipo" name="type" value={type} onChange={handleTypeChange}>
                    <FormControlLabel value="PF" control={<Radio />} label="Pessoa Fsica" />
                    <FormControlLabel value="PJ" control={<Radio />} label="Pessoa Jurdica" />                                   
                  </RadioGroup>
                </FormControl>
              </div>
              <div className="input-block">
                <TextField
                  name="cpf_cnpj"
                  label={cpfCnpjLabel}
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={cpf_cnpj}
                  onChange={handleCpfCnpjChange}                  
                  fullWidth
                  required="required"
                />
              </div>
              <div className="input-block">                
                <TextField
                  name="name"
                  label="Nome"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={name}
                  onChange={handleNameChange}                  
                  fullWidth
                  required="required"
                />
              </div>
              <div className="input-block">
                <TextField
                  name="email"
                  label="Email"
                  variant="outlined"
                  type="email"
                  autoComplete="off"
                  value={email}
                  onChange={handleEmailChange}                                
                  fullWidth
                />
              </div>
              <div className="input-block">
                <TextField
                  name="phone"
                  label="Telefone"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={phone}
                  onChange={handlePhoneChange}                              
                  fullWidth
                />
              </div>

              <div className="input-block">
                <TextField
                  select
                  name="active"
                  label="Status"
                  variant="outlined"                  
                  autoComplete="off"
                  value={active}
                  onChange={handleActiveChange}      
                  fullWidth
                  required
                >
                  <MenuItem key="1" value={true}>Ativo</MenuItem>
                  <MenuItem key="0" value={false}>Inativo</MenuItem>                      
                </TextField> 
              </div>

            </div>            
            
            <div className="accounting-form-button">
              <PrimaryButton size="large" variant="contained" type="submit">Salvar</PrimaryButton>
            </div>
          </form>
        </div>
      </BackgroundCard>
    </ClientDrawer>
  );
};

export default SuppliersCreate;