import React, { useContext, useEffect, useState } from 'react';

// Material UI
import { TextField, MenuItem } from '@material-ui/core';
import { useFormik } from 'formik';
import * as yup from 'yup';

// Custom Components
import BackgroundCard from '../../../components/BackgroundCard';
import AccountingDrawer from '../../../components/AccountingDrawer';
import PrimaryButton from '../../../components/Button';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';
import { SnackContext } from '../../../contexts/SnackContext';
import Modal from '../../../components/Modal';  

import api from '../../../services/api';

// Assets
import './index.css';

const CallsCreate = () => {
  const { setSnack } = useContext(SnackContext);
  const [openModal, setOpenModal] = useState(false);
  const [clients, setClients] = useState('');
  const [departments, setDepartments] = useState('');
  const [protocol, setProtocol] = useState('');

  const handleOpenModal = () => {
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
  };

  useEffect(() => {
    document.title = 'GestFacil | Cadastro de Departamento';
    populateSelects();
  }, [])

  const validationSchema = yup.object({
    client: yup.string()
      .required(true),
    requester: yup.string()
      .required(true),
    requesterContact: yup.string()
      .required(true),
    department: yup.string()
      .required(true),
    subject: yup.string()
      .required(true),
    priority: yup.string()
      .required(true),
    details: yup.string()
      .required(true),
  });

  const create = useFormik({
    validationSchema: validationSchema,
    initialValues: {
      client: '',
      requester: '',
      requesterContact: '',
      client: '',
      department: '',       
      subject: '',       
      priority: '',       
      details: '',       
    },
    onSubmit: (values) => {
      handleCreate(values);
    },
  });

  async function populateSelects() {
    try {      
      const response1 = await api.get('/client');
      const response2 = await api.get('/department');  
      setClients(response1.data.active);             
      setDepartments(response2.data.active);             
    } catch (err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro carregar as configuraes. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  async function handleCreate(values) {
    try {
      const res = await api.post('/call', {
         ...values
      });       
      setProtocol(res.data.createdCall.id);
      handleOpenModal();
      create.resetForm()
    } catch (err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao abrir o chamado. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err.message;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (
    <AccountingDrawer title="Chamado / Novo">
      <BackgroundCard>
        <HeaderTitle
          title="Novo Chamado"    
        />

        <div className="accounting-form-container">
          <form className="accounting-form" onSubmit={create.handleSubmit}>
            <HeaderSubtitle
              title="Dados Bsicos"
            />           

            <div className="accounting-form-admin">
              <div className="input-block">
                <TextField
                  select
                  name="client"
                  label="Cliente"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.client}
                  onChange={create.handleChange}
                  error={create.touched.client && Boolean(create.errors.client)}
                  fullWidth
                >
                  {Object.keys(clients).map(key => {

                    return (
                      <MenuItem key={`${clients[key].id}`} value={`${clients[key].id}`}>{clients[key].tradingName}</MenuItem>
                    )
                  })}
                                 
                </TextField>              
              </div>
              <div className="input-block">
                <TextField
                  name="requester"
                  label="Solicitante"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.requester}
                  onChange={create.handleChange}
                  error={create.touched.requester && Boolean(create.errors.requester)}
                  fullWidth
                />
              </div>
              <div className="input-block">
                <TextField
                  name="requesterContact"
                  label="Contato do Solicitante"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.requesterContact}
                  onChange={create.handleChange}
                  error={create.touched.requesterContact && Boolean(create.errors.requesterContact)}
                  fullWidth
                />
              </div>
              <div className="input-block">
                <TextField
                  select
                  name="department"
                  label="Departamento"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.department}
                  onChange={create.handleChange}
                  error={create.touched.department && Boolean(create.errors.department)}
                  fullWidth
                >
                  {Object.keys(departments).map(key => {
                    return (
                      <MenuItem key={`${departments[key].id}`} value={`${departments[key].id}`}>{departments[key].name}</MenuItem>
                    )
                  })}              
                </TextField>              
              </div>
              <div className="input-block">
                <TextField
                  name="subject"
                  label="Assunto"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.subject}
                  onChange={create.handleChange}
                  error={create.touched.subject && Boolean(create.errors.subject)}
                  fullWidth
                />
              </div>
              <div className="input-block">
                <TextField
                  select
                  name="priority"
                  label="Prioridade"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.priority}
                  onChange={create.handleChange}
                  error={create.touched.priority && Boolean(create.errors.priority)}
                  fullWidth
                >
                  <MenuItem value="1">Alta</MenuItem>
                  <MenuItem value="2">Mdia</MenuItem>
                  <MenuItem value="3">Baixa</MenuItem>                  
                </TextField>              
              </div>                           
            </div>

            <HeaderSubtitle
              title="Detalhes"
            /> 
            <div className="input-block">
              <TextField
                name="details"
                multiline
                label="Descreva os detalhes do chamado"
                variant="outlined"
                type="text"
                autoComplete="off"
                value={create.values.details}
                onChange={create.handleChange}
                error={create.touched.details && Boolean(create.errors.details)}
                fullWidth
              />
            </div>

            <HeaderSubtitle
              title="Anexos"
            />
            <div className="input-block">
              <p>Este recurso est em desenvolvimento. Em breve voc poder adicionar anexos aos chamados.</p>
            </div>

            <div className="accounting-form-button">
              <PrimaryButton size="large" variant="contained" type="submit">Abrir Chamado</PrimaryButton>
            </div>
          </form>
        </div>
      </BackgroundCard>
      <Modal
        open={openModal}
        onClose={handleCloseModal}
        title="Chamado cadastrado com sucesso!"
        actions={
          <>
            <PrimaryButton onClick={handleCloseModal} color="primary">OK</PrimaryButton>            
          </>
        }
      >
        <p>O nmero de protocolo : <strong>{protocol}</strong></p>
      </Modal>
    </AccountingDrawer>
  );
};

export default CallsCreate;