import { createMuiTheme } from '@material-ui/core/styles';

const UiTheme = createMuiTheme({
  typography: {
    fontFamily: [
      'Roboto',
      'sans-serif',
    ].join(','),
    fontWeight: 400,
  },
  palette: {
    primary: {
      light: '#f9fbe7',
      main: '#a3c338',
      nav: "#ffffff",
      contrastText: '#fff',
    },
    secondary: {
      main: '#000',
    },
    danger: {
      main: '#870000',
    },
    textPrimary: {
      main: '#fff'
    }
  }
});

export default UiTheme;