import React, { useContext, useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core'
import { Skeleton } from '@material-ui/lab';

import ClientDrawer from '../../../components/ClientDrawer';
import BackgroundCard from '../../../components/BackgroundCard';
import PrimaryButton from '../../../components/Button';
import BackgroundCardHeader from '../../../components/BackgroundCardHeader';
import Modal from '../../../components/Modal';
import IconButton from '../../../components/IconButton';
import { DataGrid, gridColumnLookupSelector } from '@material-ui/data-grid';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

import AddIcon from '@material-ui/icons/Add';
import CreateOutlinedIcon from '@material-ui/icons/CreateOutlined';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import WbIncandescentIcon from '@material-ui/icons/WbIncandescentOutlined';
import PowerSettingsNewOutlinedIcon 
from '@material-ui/icons/PowerSettingsNewOutlined';

import './index.css';
import api from '../../../services/api';
import { SnackContext } from '../../../contexts/SnackContext';

const useStyles = makeStyles({
  table: {
    minWidth: 'auto',
    border: '1px solid rgba(224, 224, 224, 1)',
    borderCollapse: 'separate',
    borderRadius: '8px'
  },
});

const formatStatus = (active) => { 
  return active ? 'ATIVA' : 'INATIVA';
}

const BanksList = () => {
  const classes = useStyles();
  const history = useHistory();

  const { setSnack } = useContext(SnackContext);

  const [loading, setLoading] = useState(true);
  const [openModal, setOpenModal] = useState(false);

  const [method, setMethod] = useState('');
  const [bankId, setBankId] = useState('');
  
  const [banks, setBanks] = useState('');
  
  const columns = [  
    { 
      field: 'title', 
      headerName: 'Apelido', 
      width: 250,
      sortable: false,
    },
    { 
      field: 'bank', 
      headerName: 'Banco', 
      width: 250,
      sortable: false,
    },
    { 
      field: 'ag', 
      headerName: 'Ag', 
      width: 160,
      sortable: false,
    },
    { 
      field: 'cc', 
      headerName: 'Cc', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'active', 
      headerName: 'Status', 
      width: 120,
      sortable: false,
      renderCell: (obj) => {
        return formatStatus(obj.row.active);
      }
    },
    { 
      field: 'actions', 
      headerName: 'Aes', 
      width: 200,
      sortable: false,
      renderCell: (bank) => {        
        return (<>  
         <IconButton onClick={() => {
           return history.push(`/cli/edit_bank/${bank.row.id}`)}
          }>
            <CreateOutlinedIcon />
          </IconButton>
          {
            bank.row.active 
            ?
            <IconButton onClick={() => {
              handleModalOpen(bank.row.id,'inactivate')
            }}>
              <PowerSettingsNewOutlinedIcon/> 
            </IconButton>
             :
            <>
              <IconButton onClick={() => handleActivate(bank.row.id)}>
                <WbIncandescentIcon />
              </IconButton>
            </>
          }
        </>)
      }
    }
  ];

  useEffect(() => {
    document.title = 'Contas Bancrias | GestFacil';    
    populateTable();
  }, []);

  function handleModalOpen(bank, method) {
    setBankId(bank);
    setMethod(method);    
    setOpenModal(true);
  }
  
  const handleCloseModal = () => {
    setOpenModal(false);
  };

  const handleBankChange = (data) => {
    setBanks(data);
  };

  const handleInactivate = async () => {
    try {
      await api.post(`/bank/inactivate/${bankId}`);
      handleCloseModal();
      populateTable();
      setSnack({ message: 'Conta inativada com sucesso!', type: 'success', open: true});
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao inativar a conta. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  const handleActivate = async (id) => {
    try {
      await api.post(`/bank/activate/${id}`);
      handleCloseModal();
      populateTable();
      setSnack({ message: 'Conta ativada com sucesso!', type: 'success', open: true});
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao  ativar a conta. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }
 
  async function populateTable() {
    try {
      const response = await api.get(`/bank`);
      if (response.data.banks){         
        handleBankChange(response.data.banks);    
      } else {
       setBanks([]);
      }        
      setLoading(false);
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao buscar as Contas Bancrias. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (

    <ClientDrawer title="Contas Bancrias">
      <div className="master-dashboard">
        <BackgroundCard>        
          <BackgroundCardHeader
            title="Contas Bancrias"
          >
            <Link className="buttonLink" to="/cli/register_bank"><PrimaryButton variant="contained" size="large"><AddIcon /></PrimaryButton></Link>
          </BackgroundCardHeader>
          <div style={{ height: '75vh', width: '100%', marginTop: '40px'}}>
              {(loading) ?
                <>
                  <Skeleton animation="wave" 
                    width="auto" 
                    height="57px" 
                    variant="rect" 
                  />
                  {[1, 2, 3, 4, 5, 6].map((n) =>
                    <Skeleton key={n} 
                      animation="wave" 
                      width="auto" 
                      height="65px" 
                      variant="rect" 
                      style={{ marginTop: 5 }} 
                    />
                  )}
                </>
                :
                <DataGrid rows={banks} columns={columns} pageSize={10} 
                  disableColumnMenu 
                  disableSelectionOnClick 
                />
              }
          </div>
        </BackgroundCard>
      </div>
      <Modal
          open={openModal}
          onClose={handleCloseModal}
          title={`Tem certeza que deseja ${method === 'delete' ? 'excluir' : 'inativar'} a conta?`}
          actions={
            <>
              <PrimaryButton onClick={method === 'delete' ? '' : handleInactivate}>Sim</PrimaryButton>
              <PrimaryButton onClick={handleCloseModal} color="primary">No</PrimaryButton>
            </>
          }
          >
          {method === 'delete' ? <p><strong>Cuidado:</strong> Esta ao  irreversvel!</p> : ''}
      </Modal>
    </ClientDrawer>
  );
};

export default BanksList;