import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, makeStyles, Hidden } from '@material-ui/core'
import { Skeleton } from '@material-ui/lab';

import ClientDrawer from '../../../components/ClientDrawer';
import BackgroundCard from '../../../components/BackgroundCard';
import PrimaryButton from '../../../components/Button';
import BackgroundCardHeader from '../../../components/BackgroundCardHeader';
import Modal from '../../../components/Modal';
import IconButton from '../../../components/IconButton';
import { DataGrid } from '@material-ui/data-grid';

import AddIcon from '@material-ui/icons/Add';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';

import './index.css';
import api from '../../../services/api';
import { SnackContext } from '../../../contexts/SnackContext';

const useStyles = makeStyles({
  table: {
    minWidth: 'auto',
    border: '1px solid rgba(224, 224, 224, 1)',
    borderCollapse: 'separate',
    borderRadius: '8px'
  },
});

const FilesList = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(true);
  const [files, setFiles] = useState('');
  const { setSnack } = useContext(SnackContext);
  const [fileId, setFileId] = useState('');
  const [openModal, setOpenModal] = useState(false);

  const columns = [  
    { 
      field: 'subcategory', 
      headerName: 'Subcategoria', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'dueDate', 
      headerName: 'Data de Vencimento', 
      width: 160,
      sortable: false,
    },
    { 
      field: 'value', 
      headerName: 'Valor', 
      width: 120,
      sortable: false,
    },
    { 
      field: 'status', 
      headerName: 'Status', 
      width: 120,
      sortable: false,
    },
    { 
      field: 'actions', 
      headerName: 'Aes', 
      width: 120,
      sortable: false,
      renderCell: (file) => {        
        return (<>
          <IconButton onClick={() => window.open(`${process.env.REACT_APP_API_URL}/uploads/client_files/${file.value.name}`, '_blank')}>
            <VisibilityOutlinedIcon />
          </IconButton>
          <IconButton onClick={() => handleModalOpen(file.value.id)}>
            <DeleteOutlinedIcon />
          </IconButton>
        </>)
      }
    }
  ];

  useEffect(() => {
    document.title = 'Arquivos | GestFacil';    
    populateTable();
  }, []);

  function formatDate(date) {
    date = new Date(date);
    return date.toLocaleDateString("pt-BR", {timeZone: 'UTC'});
  }

  function formatHour(date) {
    date = new Date(date);
    return date.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'});
  }

  function handleModalOpen(id) {
    setFileId(id);
    setOpenModal(true);
  }
  
  const handleCloseModal = () => {
    setOpenModal(false);
  };

  async function handleDelete() {
    try {     
      console.log(fileId)  ;
      const response = await api.delete(`/client_file/${fileId}`);
      setLoading(true);
      populateTable();
      handleCloseModal();
      setSnack({ message: 'Arquivo excludo com sucesso!', type: 'success', open: true});  
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao excluir. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  async function populateTable() {
    try {
      const response = await api.get(`/client_file`);
      if (response.data){
        

        response.data.files.forEach(item => {
          let previous = item.referenceDate;
          item.referenceDate = `${formatDate(item.referenceDate)} ${formatHour(previous)}`;   
          item.dueDate = formatDate(item.dueDate);
          item.actions = {
            name: item.name,
            id: item.id
          }
        });

        setFiles(response.data.files);

        
      } else {
        setFiles([]);
      }        
      setLoading(false);
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao buscar os arquivos. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (

    <ClientDrawer title="Arquivos">
      <div className="master-dashboard">
        <BackgroundCard>

          <BackgroundCardHeader
            title="Arquivos"
          >
            <Link className="buttonLink" to="/cli/send_file"><PrimaryButton variant="contained" size="large"><AddIcon /></PrimaryButton></Link>
          </BackgroundCardHeader>

          <div style={{ height: '75vh', width: '100%', marginTop: '40px'}}>
           
              {(loading) ?
                <>
                  <Skeleton animation="wave" width="auto" height="57px" variant="rect" />

                  {[1, 2, 3, 4, 5, 6].map((n) =>
                    <Skeleton key={n} animation="wave" width="auto" height="65px" variant="rect" style={{ marginTop: 5 }} />
                  )}

                </>
                :
                
                <DataGrid rows={files} columns={columns} pageSize={10} disableColumnMenu disableSelectionOnClick />
                
              }
            
          </div>

        </BackgroundCard>
      </div>
      <Modal
          open={openModal}
          onClose={handleCloseModal}
          title={`Tem certeza que deseja excluir o arquivo?`}
          actions={
            <>
              <PrimaryButton onClick={handleDelete}>Sim</PrimaryButton>
              <PrimaryButton onClick={handleCloseModal} color="primary">No</PrimaryButton>
            </>
          }
          >
          <p><strong>Cuidado:</strong> Esta ao  irreversvel!</p>
      </Modal>
    </ClientDrawer>

  );
};

export default FilesList;