import React from 'react';
import {
  Router,
  Switch,
  Route,
} from 'react-router-dom';

// Public Module
import ChooseModule from '../pages/public_module/ChooseModule';
import NotFound from '../pages/public_module/NotFound';
import Login from '../pages/public_module/Login';
import ForgottenPassword from '../pages/public_module/ForgottenPassword';
import InDevelopment from '../pages/public_module/InDevelopment';

import PublicRoute from './public.routes';
import PrivateRoute from './private.routes';

// Accounting Module
import AccDashboard from '../pages/accounting_module/Dashboard';

import AccOperators from '../pages/accounting_module/Operators';
import AccOperatorsCreate from '../pages/accounting_module/OperatorsCreate';
import AccOperatorsEdit from '../pages/accounting_module/OperatorsEdit';

import AccClients from '../pages/accounting_module/Clients';
import AccClientsCreate from '../pages/accounting_module/ClientsCreate';
import AccClientsEdit from '../pages/accounting_module/ClientsEdit';

import AccDepartments from '../pages/accounting_module/Departments';
import AccDepartmentsCreate from '../pages/accounting_module/DepartmentsCreate';
import AccDepartmentsEdit from '../pages/accounting_module/DepartmentsEdit';

import AccFilesList from '../pages/accounting_module/FilesList';

// Calls Module
import AccCalls from '../pages/calls_module/Calls';
import AccCallsCreate from '../pages/calls_module/CallsCreate';
import AccCallsSearch from '../pages/calls_module/CallsSearch';

// Client Module
import CliDashboard from '../pages/client_module/Dashboard';
import CliFilesList from '../pages/client_module/FilesList';
import CliFilesSend from '../pages/client_module/FilesSend';

import CliSuppliersList from '../pages/client_module/SuppliersList';
import CliSuppliersCreate from '../pages/client_module/SuppliersCreate';
import CliSuppliersEdit from '../pages/client_module/SuppliersEdit';

import CliSubcategoriesCreate from '../pages/client_module/SubcategoriesCreate';

import CliBanksList from '../pages/client_module/BanksList';
import CliBanksCreate from '../pages/client_module/BanksCreate';
import CliBanksEdit from '../pages/client_module/BanksEdit';

import CliBillsToPay from '../pages/client_module/BillsToPay';

import history from '../history';
import Temp from '../pages/temp';

const Routes = () => {
  return (
    <Router history={history}>
      <Switch>

        {/* Public Module Routes */}
        <PublicRoute exact path="/">
          <ChooseModule/>
        </PublicRoute>

        <PublicRoute exact path="/acc/login" component={() => <Login type="Escritrio Contbil"/>} />
        <PublicRoute exact path="/acc/forgotten_password" component={() => <ForgottenPassword type="Escritrio Contbil"/>} />

        <PublicRoute exact path="/cli/login" component={() => <Login type="Cliente"/>} />
        <PublicRoute exact path="/cli/forgotten_password" component={() => <ForgottenPassword type="Cliente"/>} />
       
        {/* Accounting Module */}
        <PrivateRoute type="accounting" exact path="/acc/dashboard" component={() => <AccDashboard />}/>
        
        <PrivateRoute type="accounting" exact path="/acc/clients" component={() => <AccClients />}/>
        <PrivateRoute type="accounting" exact path="/acc/create_client" component={() => <AccClientsCreate />}/>
        <PrivateRoute type="accounting" exact path="/acc/edit_client/:id" component={() => <AccClientsEdit />}/>
        
        <PrivateRoute type="accounting" exact path="/acc/operators" component={() => <AccOperators />}/>
        <PrivateRoute type="accounting" exact path="/acc/edit_operator/:id" component={() => <AccOperatorsEdit />}/>
        <PrivateRoute type="accounting" exact path="/acc/create_operator" component={() => <AccOperatorsCreate />}/>
        
        <PrivateRoute type="accounting" exact path="/acc/departments" component={() => <AccDepartments />}/>
        <PrivateRoute type="accounting" exact path="/acc/create_department" component={() => <AccDepartmentsCreate />}/>
        <PrivateRoute type="accounting" exact path="/acc/edit_department/:id" component={() => <AccDepartmentsEdit />}/>
        
        <PrivateRoute type="accounting" exact path="/acc/received_files" component={() => <AccFilesList />}/>

        {/* Calls Module */}
        <PrivateRoute type="accounting" exact path="/acc/calls" component={() => <AccCalls />}/>
        <PrivateRoute type="accounting" exact path="/acc/create_call" component={() => <AccCallsCreate />}/>
        <PrivateRoute type="accounting" exact path="/acc/search_call" component={() => <AccCallsSearch />}/>
        <PrivateRoute type="accounting" exact path="/acc/profile" component={() => <InDevelopment />}/>


        {/* Client Module */}

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/dashboard" 
          component={() => <CliDashboard />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/sent_files" 
          component={() => <CliFilesList />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/send_file" 
          component={() => <CliFilesSend />}
        />
        
        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/suppliers" 
          component={() => <CliSuppliersList />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/create_supplier" 
          component={() => <CliSuppliersCreate />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/edit_supplier/:id" 
          component={() => <CliSuppliersEdit />}
          />
        
        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/banks" 
          component={() => <CliBanksList />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/register_bank" 
          component={() => <CliBanksCreate />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/edit_bank/:id" 
          component={() => <CliBanksEdit />}
        />
        
        {/* Client / Subcategories */}

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/subcategories" 
          component={() => <InDevelopment />}
        />

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/create_subcategory" 
          component={() => <CliSubcategoriesCreate />}
        />

        {/* Client / Bills to Pay */}
        
        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/bills_to_pay" 
          component={() => <CliBillsToPay />}
        />
        
        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/operators" 
          component={() => <InDevelopment />}
        />        

        <PrivateRoute 
          type="client" 
          exact 
          path="/cli/profile" 
          component={() => <InDevelopment />}
        />

        <Route component={NotFound} />
      </Switch>
    </Router>
  )
}

export default Routes;