import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow, 
  makeStyles, 
  Hidden,
  TextField,
  MenuItem,
  ListItemSecondaryAction,  
} from '@material-ui/core'

import {  useFormik } from 'formik';

import { Skeleton } from '@material-ui/lab';

import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import CreateOutlinedIcon from '@material-ui/icons/CreateOutlined';
import AddIcon from '@material-ui/icons/Add';
import PrintIcon from '@material-ui/icons/PrintOutlined';

import ClientDrawer from '../../../components/ClientDrawer';
import BackgroundCard from '../../../components/BackgroundCard';
import PrimaryButton from '../../../components/Button';
import BackgroundCardHeader from '../../../components/BackgroundCardHeader';
import { DataGrid } from '@material-ui/data-grid';
import FutureCalendar from '../../../components/FutureCalendar';
import IconButton from '../../../components/IconButton';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';

import { AuthContext } from '../../../contexts/AuthContext';
import { SnackContext } from '../../../contexts/SnackContext';
import api from '../../../services/api';
import './index.css';

const useStyles = makeStyles({
  table: {
    minWidth: 'auto',
    border: '1px solid rgba(224, 224, 224, 1)',
    borderCollapse: 'separate',
    borderRadius: '8px'
  },
});

const BillsToPay = () => {
  const classes = useStyles();

  const [banks, setBanks] = useState('');
  const [loading, setLoading] = useState(false); 
  const [files, setFiles] = useState([]);
  
  const { setSnack } = useContext(SnackContext);

  const [startDueDate, setStartDueDate] = useState(new Date());
  const [finalDueDate, setFinalDueDate] = useState(new Date());

  const [startSendDate, setStartSendDate] = useState(new Date("2021-01-02"));
  const [finalSendDate, setFinalSendDate] = useState(new Date());
  
  const columns = [  
    { 
      field: 'actions', 
      headerName: 'Aes', 
      width: 120,
      sortable: false,
      renderCell: (file) => {        
        return (<>
          <IconButton onClick={() => window.open(`${process.env.REACT_APP_API_URL}/uploads/client_files/${file.value.name}`, '_blank')}>
            <VisibilityOutlinedIcon />
          </IconButton>

           <IconButton onClick={() => handleStatusChange(file.value.id)}>
            <CreateOutlinedIcon />
          </IconButton>         
        </>)
      }
    },
    { 
      field: 'category', 
      headerName: 'Categoria', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'subcategory', 
      headerName: 'Subcategoria', 
      width: 200,
      sortable: false,
    },
    { 
      field: 'formatedStatus', 
      headerName: 'Status', 
      width: 120,
      sortable: false,
      renderCell: ((status) => {        
        return (<>
         <span style={{color: `${
           status.value === 'PENDENTE' ? 'red' : 'blue' }`}}>{status.value}</span>     
        </>)
      })
    },
    { 
      field: 'createdAt', 
      headerName: 'Data de Envio', 
      width: 160,
      sortable: false,
    },
    { 
      field: 'dueDate', 
      headerName: 'Data de Vencimento', 
      width: 160,
      sortable: false,
    },
    { 
      field: 'value', 
      headerName: 'Valor', 
      width: 120,
      sortable: false,
    },
    { 
      field: 'notes', 
      headerName: 'Obs.', 
      width: 400,
      sortable: false,
    }    
  ];

  useEffect(() => {
    document.title = 'Contas a Pagar | GestFacil';    
    populateSelects();
  }, []);

  const filterForm = useFormik({
    initialValues: {
      client: 'Todas',
      status: 'Todos',
      startSendDate: '',
      finalSendDate: '',
      startDueDate: '',
      finalDueDate: ''   
    },
    onSubmit: (values) => {
      handleFilter(values);
    }
  });

  const handleFilter = async (values) => {

    values.startSendDate = formatDateUS(startSendDate);
    values.finalSendDate = formatDateUS(finalSendDate);
    values.startDueDate = formatDateUS(startDueDate);
    values.finalDueDate = formatDateUS(finalDueDate);

    const response = await api.get("/client_file", {
      params: {
        ...values
      }
    });

    response.data.files.forEach(file => {
      file.dueDate = formatDate(file.dueDate);
      file.createdAt = `${formatDate(file.createdAt)} ${formatHour(file.createdAt)}`;
      file.actions = {
        name: file.name,
        id: file.id
      }
      file.formatedStatus = file.status;

      return;
    });
    setFiles(response.data.files);   
  }

  const populateSelects = async () => {    
    try {
      const response = await api.get('/bank');   
      setBanks(response.data.banks);
    } catch (err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro carregar as configuraes. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  const formatDate = (date) => {
    date = new Date(date);
    return date.toLocaleDateString('pt-BR', {timeZone: 'UTC'});
  };

  const formatDateUS = (date) => {
    date = new Date(date);
    return date.toLocaleDateString('en-US', {timeZone: 'UTC'});
  };

  const formatHour = (date) => {
    date = new Date(date);
    return date.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'});
  }

 const handleStatusChange = async (id) => {
    try {
      await api.put(`client_file/${id}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      });
      filterForm.handleSubmit();
      setSnack({ message: 'Status alterado com sucesso!', type: 'success', open: true})
    } catch(err) {
       if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao alterar o status. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (
    <ClientDrawer title="Contas a Pagar">
      <div className="master-dashboard">
        <BackgroundCard>
          <BackgroundCardHeader title="Contas a Pagar">
            <Link className="buttonLink" to="/cli/send_file">
              <PrimaryButton variant="contained" size="large">
                <AddIcon />
              </PrimaryButton>
            </Link>
            <PrimaryButton variant="contained" size="large" disabled>
                <PrintIcon />
            </PrimaryButton>
          </BackgroundCardHeader>
          <HeaderSubtitle/>

          <div className="filter-form-container">
            <form className="filter-form" onSubmit={filterForm.handleSubmit} id="filter-form">
              <div className="filter-form-basic">
                <div className="input-block">
                  <TextField
                    select
                    name="bank"
                    label="Conta Bancria"
                    variant="outlined"
                    type="text"
                    autoComplete="off"
                    value={filterForm.values.bank}
                    onChange={filterForm.handleChange}
                    error={filterForm.touched.bank && Boolean(filterForm.touched.bank)}
                    fullWidth                 
                  >                    
                    <MenuItem key="0" value="Todas">Todas</MenuItem>
                    {Object.keys(banks).map(key => {
                      return (
                        <MenuItem 
                          key={`${banks[key].id}`} 
                          value={`${banks[key].id}`}
                        >
                          {banks[key].title}
                        </MenuItem>
                      )
                    })}
                  </TextField>
                </div>
                <div className="input-block">
                  <TextField
                    select
                    name="status"
                    label="Status"
                    variant="outlined"
                    type="text"
                    autoComplete="off"
                    value={filterForm.values.status}
                    onChange={filterForm.handleChange}
                    error={filterForm.touched.status  && Boolean(filterForm.touched.status)}
                    fullWidth                 
                    >
                    <MenuItem key="1" value="Todos">Todos</MenuItem>
                    <MenuItem key="2" value="PENDENTE">Pendente</MenuItem>
                    <MenuItem key="3" value="AGENDADO">Agendado</MenuItem>
                  </TextField>
                </div>
                <div className="input-block">
                  <FutureCalendar                    
                    name="startDueDate"
                    label="Data de Vencimento Inicial"
                    inputVariant="outlined"                    
                    autoComplete="off"
                    value={startDueDate}
                    onChange={setStartDueDate}           
                    fullWidth
                  />
                </div>
                <div className="input-block">
                  <FutureCalendar                    
                    name="finalDueDate"
                    label="Data de Vencimento Final"
                    inputVariant="outlined"                    
                    autoComplete="off"
                    value={finalDueDate}
                    onChange={setFinalDueDate}                
                    fullWidth
                  />
                </div>
                <div className="input-block">
                  <FutureCalendar                    
                    name="startSendDate"
                    label="Data de Envio Inicial"
                    inputVariant="outlined"                    
                    autoComplete="off"
                    value={startSendDate}
                    onChange={setStartSendDate}                 
                    fullWidth
                  />
                </div>
                <div className="input-block">
                  <FutureCalendar                    
                    name="finalSendDate"
                    label="Data de Envio Final"
                    inputVariant="outlined"                    
                    autoComplete="off"
                    value={finalSendDate}
                    onChange={setFinalSendDate}               
                    fullWidth
                  />
                </div>
              </div>
              <div className="filter-form-button">
                <PrimaryButton size="large" variant="contained" type="submit">Buscar</PrimaryButton>
              </div>
            </form>
          </div>
          <div style={{ height: '75vh', width: '100%', marginTop: '40px'}}>        
            <DataGrid rows={files} columns={columns} pageSize={10} disableColumnMenu disableSelectionOnClick />                         
          </div>
        </BackgroundCard>
      </div>
    </ClientDrawer>
  );
};

export default BillsToPay;