import React, { useContext, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

// Material UI
import { TextField, MenuItem } from '@material-ui/core';
import { useFormik } from 'formik';
import MaskedInput from 'react-text-mask';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

// Custom Components
import BackgroundCard from '../../../components/BackgroundCard';
import ClientDrawer from '../../../components/ClientDrawer';
import PrimaryButton from '../../../components/Button';
import FutureCalendar from '../../../components/FutureCalendar';
import { HeaderTitle, HeaderSubtitle } from '../../../components/HeaderTitle';
import Subcategories from '../../../services/subcategories';
import Modal from '../../../components/Modal';

import { SnackContext } from '../../../contexts/SnackContext';
import api from '../../../services/api';
import './index.css';

const defaultMaskOptions = {
  prefix: '',
  suffix: '',
  includeThousandsSeparator: true,
  thousandsSeparatorSymbol: '.',
  allowDecimal: true,
  decimalSymbol: ',',
  decimalLimit: 2, 
  integerLimit: 7,
  allowNegative: false,
  allowLeadingZeroes: false,
}

const currencyMask = createNumberMask(defaultMaskOptions);

const CurrencyMask = (props) => {
  const { inputRef, ...other } = props;

  return (
    <MaskedInput
      {...other}
      ref={(ref) => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={currencyMask}
      placeholderChar={'\u2000'}
      guide={false}
    />
  );
}

const FilesSend = () => {
  const { setSnack } = useContext(SnackContext);
  const history = useHistory();
  const [subcategories, setSubcategories] = useState(Subcategories.receitas);    
  const [dueCalendar, setDueCalendar] = useState(new Date());
  const [fileName, setFileName] = useState('Nenhum arquivo selecionado.');
  const [lockedSubmit, setLockedSubmit] = useState(false);
  const [openModal, setOpenModal] = useState(false);

  const create = useFormik({
    initialValues: {
      category: 'Receita',
      subcategory: '',     
      value: '',  
      dueDate: '',
      notes: '', 
      document: null,
    },
    onSubmit: (values) => {
      handleCreate(values);
    },
  });

  const handleOpenModal = () => {     
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
  };

  const handleRedirect = () => {
    handleCloseModal();
    history.push("/cli/sent_files");
  }
  
  useEffect(() => {
    document.title = 'GestFacil | Enviar Arquivo'

    if(create.values.category === 'Receita') {
      setSubcategories(Subcategories.receitas);
    }

    if(create.values.category === 'Despesa') {
      setSubcategories(Subcategories.despesas);
    }

    if(create.values.category === 'Ativo') {
      setSubcategories(Subcategories.ativos);
    }
  }, [create.values.category]);


  
  async function handleCreate(values) {
    if (lockedSubmit) {
      setSnack({ message: 'Aguarde o envio do arquivo!', type: 'error', open: true });
    } else {
      try {
        setLockedSubmit(true);           
        values.dueDate = formatDate(dueCalendar);

        const formData = new FormData();
        formData.append("category", values.category);
        formData.append("subcategory", values.subcategory);    
        formData.append("dueDate", values.dueDate);
        formData.append("value", values.value);
        formData.append("notes", values.notes);
        formData.append("document", values.document);                        
        
        await api.post('/client_file', formData)        
        
        setSnack({ message: 'Arquivo enviado com sucesso!', type: 'success', open: true });
        create.resetForm();
        document.getElementById('contained-button-file').value="";
        setFileName("Nenhum arquivo selecionado.");
        setLockedSubmit(false);

        handleOpenModal();
      } catch (err) { 
        setSnack({ message: `${err.response.data.err.message}`, type: 'error', open: true });
        setLockedSubmit(false);
      }
    }
  }

  function formatDate(val) {
    let d = new Date(val),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2)
      month = '0' + month;
    if (day.length < 2)
      day = '0' + day;

    return [year, month, day].join('-');
  }

  return (
    <ClientDrawer title="Arquivos / Enviar">
      <BackgroundCard>
        <HeaderTitle
          title="Enviar Arquivo"
          subtitle="Preencha todos os campos corretamente."
        />

        <div className="file-form-container">
          <form className="file-form" onSubmit={create.handleSubmit} encType="multipart/form-data">
            <HeaderSubtitle
              title="Informaes"
            />
            <div className="file-form-basic">
              <div className="input-block">
              <FormControl component="fieldset">
                <FormLabel component="legend">Categoria</FormLabel>
                <RadioGroup row aria-label="categoria" name="category" value={create.values.category} onChange={create.handleChange}>
                  <FormControlLabel value="Receita" control={<Radio />} label="Receita" />
                  <FormControlLabel value="Despesa" control={<Radio />} label="Despesa" />
                  <FormControlLabel value="Ativo" control={<Radio />} label="Ativo" />                
                </RadioGroup>
              </FormControl>
              </div>
              <div className="input-block">
                <TextField
                    select
                    name="subcategory"
                    label="Subcategoria"
                    variant="outlined"
                    type="text"
                    autoComplete="off"
                    value={create.values.subcategory}
                    onChange={create.handleChange}
                    error={create.touched.subcategory && Boolean(create.errors.subcategory)}
                    fullWidth
                    required
                  >
                    {Object.keys(subcategories).map(key => {

                      return (
                        <MenuItem key={`${subcategories[key].title}`} value={`${subcategories[key].title}`}>{`${subcategories[key].title}`}</MenuItem>
                      )
                    })}
                                  
                  </TextField>                           
              </div>              
              <div className="input-block">
                <TextField
                  name="value"
                  label="Valor"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.value}
                  onChange={create.handleChange}
                  error={create.touched.value && Boolean(create.errors.value)}
                  fullWidth
                  InputProps={{
                    inputComponent: CurrencyMask,
                  }}
                  required
                />             
              </div>

              <div className="input-block">
                <FutureCalendar
                  name="dueDate"
                  label="Data de Vencimento"
                  inputVariant="outlined"
                  autoComplete="off"
                  value={dueCalendar}
                  onChange={setDueCalendar}
                  fullWidth
                />
              </div>

              <div className="input-block">            
                <TextField                  
                  name="notes"
                  label="Observaes"
                  variant="outlined"
                  type="text"
                  autoComplete="off"
                  value={create.values.notes}
                  onChange={create.handleChange}
                  error={create.touched.notes && Boolean(create.errors.notes)}
                  fullWidth                 
                  />                 
              </div>
            </div>

            <HeaderSubtitle title="Arquivo"/>
           
            <div className="input-block">
              <p id="fileName">{fileName}</p> <br/>
             
              <input                               
                id="contained-button-file"
                type="file"
                name="document"
                onChange={(event) => {
                  create.setFieldValue("document", event.currentTarget.files[0]);
                  setFileName(event.currentTarget.value);                  
                }}
                hidden
                required
              />
              <label htmlFor="contained-button-file">
                <PrimaryButton variant="contained" component="span">
                  Selecionar Imagem
                </PrimaryButton>
              </label>
            </div>         
            <div className="file-form-button">
              <PrimaryButton size="large" variant="contained" type="submit">Enviar</PrimaryButton>
            </div>
          </form>
        </div>
      </BackgroundCard>
      <Modal
          open={openModal}
          onClose={handleCloseModal}
          title={`Voc deseja enviar outro documento?`}
          actions={
            <>
              <PrimaryButton onClick={handleRedirect} color="primary">No</PrimaryButton>
              <PrimaryButton onClick={handleCloseModal}>Sim</PrimaryButton>
            </>
          }
        >
      </Modal>
    </ClientDrawer>    
  );
};

export default FilesSend;