import React, { useContext, useEffect } from 'react';
import { Redirect } from 'react-router-dom';

// Material UI
import Box from '@material-ui/core/Box';

// Icons
import PhoneIcon from '@material-ui/icons/PhoneOutlined';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import BusinessCenterOutlinedIcon from '@material-ui/icons/BusinessCenterOutlined';

// Custom Components
import CardLink from '../../../components/CardLink';
import BackgroundCard from '../../../components/BackgroundCard';
import AccountingDrawer from '../../../components/AccountingDrawer';
import { HeaderTitle } from '../../../components/HeaderTitle';
import { AuthContext } from '../../../contexts/AuthContext';

// Assets
import './index.css';

const MasterDashboard = () => {
  const { user, role } = useContext(AuthContext);
  
  useEffect(() => {
    if(role !== 'accounting') {
      <Redirect to="/"/>
    }
    document.title = 'GestFacil | Dashboard';
  }, [role])

  return (
    <AccountingDrawer title="Dashboard">
      <BackgroundCard>
        <div className="master-dashboard">
          <HeaderTitle
            title={`Ol, ${user.name}!`}
            subtitle="Utilize os cards abaixo ou o menu lateral para comear."
          />

          <Box
            display="flex"
            flexWrap="wrap"
            flexDirection="row"
            justifyContent="left"
            alignItems="left"
            css={{ marginTop: 20 }}
          >
            <CardLink link="/acc/clients" icon={<BusinessCenterOutlinedIcon/>}>
              Clientes
            </CardLink>        
            
            <CardLink link="/acc/received_files" icon={<DescriptionOutlinedIcon/>}>
              Arquivos
            </CardLink>

            <CardLink link="/acc/calls" icon={<PhoneIcon />}>
              Chamados
            </CardLink>
          </Box>

        </div>
      </BackgroundCard>
    </AccountingDrawer>

  );
};


export default MasterDashboard;