import React, { useEffect, useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, makeStyles } from '@material-ui/core'
import { Skeleton } from '@material-ui/lab';

import AccountingDrawer from '../../../components/AccountingDrawer';
import BackgroundCard from '../../../components/BackgroundCard';
import PrimaryButton from '../../../components/Button';
import BackgroundCardHeader from '../../../components/BackgroundCardHeader';
import ActionButton from '../../../components/ActionButton';
import Modal from '../../../components/Modal';

import AddIcon from '@material-ui/icons/Add';

import api from '../../../services/api';
import './index.css';
import { SnackContext } from '../../../contexts/SnackContext';

const useStyles = makeStyles({
  table: {
    minWidth: 'auto',
    border: '1px solid rgba(224, 224, 224, 1)',
    borderCollapse: 'separate',
    borderRadius: '8px'
  },
});

const Clients = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(true);
  const [activeClients, setActiveClients] = useState([]);
  const [inactiveClients, setInactiveClients] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [clientId, setClientId] = useState('');
  const [method, setMethod] = useState('');
  const { setSnack } = useContext(SnackContext);

  useEffect(() => {
    document.title = 'GestFacil | Clientes'
    populateTable();
  }, [])

  function status(status) {
    if (status === true)
      return 'Ativo';
    else {
      return 'Inativo';
    }
  }

  const populateTable = async () => {
    const response = await api.get('/client')
    setActiveClients(response.data.active)
    setInactiveClients(response.data.inactive)
    setLoading(false)
  }

  const handleOpenModal = (id, method) => {
    setClientId(id);
    setMethod(method);   
    setOpenModal(true, method);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
  };

  async function handleInactivate() {
    try {     
      const response = await api.post(`/inactivate_client/${clientId}`);
      handleCloseModal();
      setSnack({ message: 'Cliente inativado com sucesso! ', type: 'success', open: true});
      setLoading(true);
      populateTable();
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao inativar o cliente. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  async function handleActivate() {
    try {     
      const response = await api.post(`/activate_client/${clientId}`);
      handleCloseModal();
      setSnack({ message: 'Cliente ativado com sucesso! ', type: 'success', open: true});
      setLoading(true);
      populateTable();
    } catch(err) {
      if(!err.response){       
        setSnack({ message: 'Encontramos um erro ao ativar o cliente. Entre em contato com o suporte! ' + err, type: 'error', open: true})
      }
      else {        
        let msg = err.response.data.err;
        setSnack({ message: `${msg}`, type: 'error', open: true });
      }
    }
  }

  return (

    <AccountingDrawer title="Clientes">
      <div className="master-dashboard">
        <BackgroundCard>

          <BackgroundCardHeader
            title="Clientes"
          >
            <Link className="buttonLink" to="/acc/create_client"><PrimaryButton variant="contained" size="large"><AddIcon /></PrimaryButton></Link>
          </BackgroundCardHeader>

          <div className="content-table">
            <TableContainer>
              {(loading) ?
                <>
                  <Skeleton animation="wave" width="auto" height="57px" variant="rect" />

                  {[1, 2, 3, 4, 5, 6].map((n) =>
                    <Skeleton key={n} animation="wave" width="auto" height="65px" variant="rect" style={{ marginTop: 5 }} />
                  )}

                </>
                :
                <Table className={classes.table} aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell>CPF/CNPJ</TableCell>
                      <TableCell>Nome</TableCell>
                      <TableCell>E-mail</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {activeClients.map((items) => (
                      <TableRow key={items.id}>
                        <TableCell component="th" scope="row">
                          {items.cpfCnpj}
                        </TableCell>
                        <TableCell>{items.companyName}</TableCell>
                        <TableCell>{items.email}</TableCell>
                        <TableCell>{status(items.active)}</TableCell>
                        <TableCell align="right">
                          <ActionButton
                            key={items.id}
                            variant="contained"
                            size="small"
                            icon="more_vert"
                            d
                            actions={[
                              { text: 'Visualizar', to: `/acc/details_client/${items.id}`  },
                              { text: 'Editar', to: `/acc/edit_client/${items.id}` },
                              { text: 'Inativar', onClick: () => handleOpenModal(items.id, 'inactivate')}]}                            
                          />
                        </TableCell>
                      </TableRow>
                    ))}
                    {inactiveClients.map((items) => (
                      <TableRow key={items.id}>
                        <TableCell component="th" scope="row">
                          {items.cpfCnpj}
                        </TableCell>
                        <TableCell>{items.companyName}</TableCell> 
                        <TableCell>{items.email}</TableCell>                       
                        <TableCell>{status(items.active)}</TableCell>
                        <TableCell align="right">
                          <ActionButton
                              key={items.id}
                              variant="contained"
                              size="small"
                              icon="more_vert"
                              d
                              actions={[
                                  { text: 'Visualizar', to: `/acc/details_client/${items.id}`  },
                                  { text: 'Editar', to: `/acc/edit_client/${items.id}` },
                                  { text: 'Ativar', onClick: () => handleOpenModal(items.id, 'activate')}]}                            
                            />
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              }
            </TableContainer>
          </div>
        </BackgroundCard>
        <Modal
          open={openModal}
          onClose={handleCloseModal}
          title={`Voc deseja ${method === 'inactivate' ? 'inativar' : 'ativar'} esse cliente?`}
          actions={
            <>
              <PrimaryButton onClick={handleCloseModal} color="primary">Cancelar</PrimaryButton>
              <PrimaryButton onClick={method === 'inactivate' ? handleInactivate : handleActivate}>Sim</PrimaryButton>
            </>
          }
        >
          {/* {method === 'inactivate' ? <p>Inativando o cliente voc o impedir de acessar o sistema at reativ-lo novamente.</p> : ''} */}
      </Modal>
      </div>
    </AccountingDrawer>

  );
};

export default Clients;